import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { AnimationTransitionEvent, trigger, state, style, transition, animate } from '@angular/core';

import { Icon } from './model';

@Component({
    selector: 'ccc-dock',
    templateUrl: 'dock.component.html',
    styleUrls: ['dock.component.scss'],
    animations: [
        trigger('leftFadeOut', [
            state('inactive', style({
                transform: 'translateX(0)',
                opacity: '1.0'
            })),
            state('active', style({
                transform: 'translateX(-10%)',
                opacity: '0.0'
            })),
            transition('inactive => active', animate('200ms ease-in')),
        ]),
        trigger('leftFadeIn', [
            state('inactive', style({
                transform: 'translateX(10%)',
                opacity: '0.0'
            })),
            state('active', style({
                transform: 'translateX(0%)',
                opacity: '1.0'
            })),
            transition('inactive => active', animate('200ms ease-in')),
        ]),
        trigger('rightFadeOut', [
            state('inactive', style({
                transform: 'translateX(0)',
                opacity: '1.0'
            })),
            state('active', style({
                transform: 'translateX(15%)',
                opacity: '0.0'
            })),
            transition('inactive => active', animate('200ms ease-in')),
        ]),
        trigger('rightFadeIn', [
            state('inactive', style({
                transform: 'translateX(-15%)',
                opacity: '0.0'
            })),
            state('active', style({
                transform: 'translateX(0%)',
                opacity: '1.0'
            })),
            transition('inactive => active', animate('200ms ease-in')),
        ])
    ]
})
export class DockComponent implements OnInit {

    @Input() icon: Icon[];
    @Output() iconClick: EventEmitter<Icon>;

    public curIndexOfRow = 0;
    public gridItem: any[] = [];
    public defaultIconImg = defaultIconImg;
    /**
     * animation variable
     */
    public aniStateOfNext = 'inactive';
    public aniStateOfPre = 'inactive';
    /**
     * animation set
     */
    aniOfPreStart() {

        this.aniStateOfPre = 'active';
    }

    public aniOfNextStart() {

        this.aniStateOfNext = 'active';
    }
    aniOfPreDone(event: AnimationTransitionEvent) {

        if (event.fromState == 'inactive' && event.toState == 'active') {
            setTimeout(() => {
                this.aniStateOfPre = 'inactive';
                // this.curIndexOfRow = this.circleNumber[this.curIndexOfRow].pre;
                this.curIndexOfRow = this.preNumber(this.curIndexOfRow);
            }, 100)

        }
    }
    aniOfNextDone(event: AnimationTransitionEvent) {

        if (event.fromState == 'inactive' && event.toState == 'active') {
            setTimeout(() => {
                this.aniStateOfNext = 'inactive';
                // this.curIndexOfRow = this.circleNumber[this.curIndexOfRow].next;
                this.curIndexOfRow = this.nextNumber(this.curIndexOfRow);
            }, 100)

        }
    }
    /**
     * 建構函式
     */
    constructor() {

        this.icon = this.icon ? this.icon : [];
        this.iconClick = new EventEmitter<Icon>();
    }
    /**
     * ng 初始化
     */
    ngOnInit() {

        var count = -1;
        for (var i = 0; i < this.icon.length; i++) {
            if (i % 5 == 0) {
                count++;
                this.gridItem[count] = [];
                this.gridItem[count].push(this.icon[i]);
            }
            else {
                this.gridItem[count].push(this.icon[i]);
            }
        }
    }
    /**
     *  點選Icon
     *  @param {Icon} icon
     */
    private onClick(icon: Icon) {

        this.iconClick.emit(icon);
    }
    /**
     * 下一列
     */
    private next() {

        this.aniOfNextStart();
    }
    /**
     * 上一列
     */
    private back() {

        this.aniOfPreStart();
    }
    private nextNumber(num: number) {

        if (num == this.gridItem.length - 1) {

            return 0;
        } else {
            return num + 1;
        }
    }
    private preNumber(num: number) {

        if (num == 0) {

            return this.gridItem.length - 1;
        } else {
            return num - 1;
        }
    }
}
var defaultIconImg: string = 'data:image/png;base64,';


